import React, {Component} from 'react';
import './RatingsFilter.css';

class RatingsFilter extends Component {
    render() {
        return (
            <div className="ratings-filter">
                <ul className="filter-wrapper">
                    <li className="filter-item on">(1914)</li>
                    <li className="filter-item">(1832)</li>
                    <li className="filter-item off">(82)</li>
                    <li className="filter-item">(22)</li>
                    <li className="filter-item">(13)</li>
                </ul>
            </div>
        );
    }
}

export default RatingsFilter;




