import React, {Component} from 'react';
import './Tab.css';

class Tab extends Component {

    componentDidMount(){
        // tab
        //tab
        let wrapper=document.querySelector('.shop-tab-main');
        let tab=document.querySelector('.shop-tab');
        wrapper.style.height=document.body.clientHeight-tab.clientHeight+'px';
    }

    render() {
        return (
            <div className="shop-tab">
                <ul>
                    <li className={this.props.currentTabIndex===0?'on':''} onClick={this.props.handleChangeTab.bind(this,0)}><span></span></li>
                    <li className={this.props.currentTabIndex===1?'on':''} onClick={this.props.handleChangeTab.bind(this,1)}><span></span></li>
                    <li className={this.props.currentTabIndex===2?'on':''} onClick={this.props.handleChangeTab.bind(this,2)}><span></span></li>
                </ul>
            </div>
        );
    }
}

export default Tab;




